<script setup lang="ts">
import { useI18n } from "vue-i18n";
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type {
  IDataTypeInstance,
  ICacheEntitiesAttributeInSetCache,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesFloatValueConfiguration } from "./models";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesFloatValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  remoteAttributes?: ICacheEntitiesAttributeInSetCache[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesFloatValueConfiguration>({
  set: ({ max, min, precision, ...rest }) =>
    ({
      precision: Number(precision),
      max: Number(max),
      min: Number(min),
      ...rest,
    }) as IDataTypesFloatValueConfiguration,
});

const { t } = useI18n();
</script>

<template>
  <ACol>
    <ATextField
      v-model.number="configuration.precision"
      :label="t('datahub-plugin.floatValue.precision')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.min"
      :label="t('datahub-plugin.floatValue.minLength')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.max"
      :label="t('datahub-plugin.floatValue.maxLength')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="t('datahub-plugin.floatValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>

<i18n>
{
  "en": {
    "datahub-plugin": {
      "floatValue": {
        "precision": "Precision",
        "minLength": "Min length",
        "maxLength": "Max length",
        "readOnly": "Read only"
      }
    }
  },
  "de": {
    "datahub-plugin": {
      "floatValue": {
        "precision": "Präzision",
        "minLength": "Min Länge",
        "maxLength": "Max Länge",
